<template>
  <div class="distWrap">
      <header>
      <router-link to="/shop"><i class="el-icon-arrow-left"></i></router-link>
      <div class="ellipsis">确认订单</div>
      <router-link to="/profile"><i class="el-icon-user"></i></router-link>
      </header>
      <section>
          <router-link to="/chooseaddress" class="address_container">
              <div class="address_empty_left">
                <i class="location_icon el-icon-location-outline"></i>
                <div class="address_detail_container">
                    <div class="header">
                        <span>{{name}}</span>
                        <span>&nbsp;先生</span>
                        <span>&nbsp;{{phone}}</span>
                    </div>
                    <div class="address_detail">
                        <span>null</span>
                        <p>{{address}}</p>
                    </div>
                </div>
              </div>
              <i class="address_empty_right el-icon-arrow-right"></i>
          </router-link>
          <section class="delivery_model container_style">
              <p class="deliver_text">送达时间</p>
              <section class="deliver_time">
                  <p>尽快送达 | 预计 {{distObj.delivery_reach_time}}</p>
                  <p>蜂鸟专送</p>
              </section>
          </section>
          <section class="pay_way container_style">
              <div class="header_style">
                  <span>支付方式</span>
                  <div class="more_type">
                      <span>在线支付</span>
                      <i class="address_empty_right el-icon-arrow-right" @click="choosefu"></i>
                  </div>
              </div>
              <section class="hongbo">
                  <span>红包</span><span>暂时只在饿了么 APP 中支持</span>
              </section>
          </section>
          <section class="food_list">
              <div class="header1">
                  <img src="https://elm.cangdu.org/img/170101d0f1463577.png" alt="">
                  <span>{{shopName}}</span>
              </div>
              <ul class="food_list_ul">
                  <li class="food_item_style" v-for="c in carArr" :key="c.name" >
                      <p class="food_name ellipsis">{{c.name}}{{c.specs == ''?'':'-'+c.specs}}</p>
                      <div class="num_price">
                          <span>x{{c.num}}</span><span>¥{{c.price}}</span>
                      </div>
                  </li>
              </ul>
              <div class="food_item_style">
                  <p class="food_name ellipsis">餐盒</p>
                  <div class="num_price">
                      <span></span>
                      <span>¥ {{canhe}}</span>
                  </div>
              </div>
              <div class="food_item_style">
                  <p class="food_name ellipsis">配送费</p>
                  <div class="num_price">
                      <span></span>
                      <span>¥ {{peisong}}</span>
                  </div>
              </div>
              <div class="food_item_style">
                  <p class="food_name ellipsis">订单 ¥{{peisong + canhe + +allPrice}}</p>
                  <div class="num_price">
                      <span>待支付¥{{peisong + canhe + +allPrice}}</span>
                      <span></span>
                  </div>
              </div>
          </section>
          <section class="pay_way container_style">
              <router-link to="/remark" class="header_style">
                  <span>订单备注</span>
                  <div class="more_type">
                      <span class="ellipsis">口味、偏好等</span>
                      <i class="el-icon-arrow-right"></i>
                  </div>
              </router-link>
              <router-link to="/invoice" class="hongbo support_is_available">
                   <span>发票抬头</span>
                  <span>不需要开发票<i class="el-icon-arrow-right"></i></span>
              </router-link>
                  
          </section>
          <section class="confrim_order">
              <p>待支付 ¥{{peisong + canhe + +allPrice}}</p>
              <p @click="goxiadan">确认下单</p>
          </section>
            <div class="cover" v-if="isShow"></div>
          <el-collapse-transition>
          <div class="choose_type_Container"  v-if="isShow">
              <div class="head">支付方式</div>
              <ul>
                  <li>
                      <span>货到付款<span>（商家不支持货到付款）</span></span>
                      <i class="address_empty_right el-icon-success"></i>
                  </li>
                  <li class="choose">
                      <span>在线支付</span>
                      <i class="address_empty_right el-icon-success" style="color:green" @click="goback"></i>
                  </li>
              </ul>
          </div></el-collapse-transition>
      </section>
  </div>
</template>

<script>
import {mapState} from "vuex";
export default {
name:'dist',
data(){
    return{
        username:'',
        placeNameHistory:'',
        geohash:'',
        shopId:'',
        carArr: [],
        shopCarts: null,
        shopName:'',
        entities:[],
        distObj:{},
        thisAddress:0,
        addressMsg:'',
        isShow:false,
        canhe:'',
        peisong:'',
        allPrice:'',
        zongjia:'',
        userid:'',
        name:'',
        phone:'',
        address:''
    }
},
created(){
    this.allPrice = localStorage.getItem('allprice');
    this.thisAddress = 0;
    this.thisAddress = localStorage.getItem('thisAddress');
    this.shopName = localStorage.getItem('shopName');
    this.shopId = localStorage.getItem('shopId');
    this.userid = localStorage.getItem('userid');
    this.axios.get("/v1/users/"+this.userid+"/addresses",{
            params:{
                user_id:this.userid,
            }
        }).then(res=>{
            // console.log(res)
            this.addressMsg = res;
            // console.log(res[0].name)
            this.name = this.addressMsg[this.thisAddress].name;
            this.phone = this.addressMsg[this.thisAddress].phone;
            this.address = this.addressMsg[this.thisAddress].address;
        }).catch(err=>{})
    this.username = localStorage.getItem('username');
    this.placeNameHistory = localStorage.getItem("placeNameHistory")
    let hisArr = JSON.parse(localStorage.getItem("placeHistory"))
    let newArr = hisArr.filter(v=>{
                return v.name == this.placeNameHistory
            })
    this.geohash = newArr[0].geohash;
    this.$store.commit("init_buy_cart");
    this.carArr = [];
    this.shopCarts = this.$store.state.cartList;
        Object.keys(this.shopCarts).forEach(shopId => {
            Object.keys(this.shopCarts[shopId]).forEach(category_id => {
                Object.keys(this.shopCarts[shopId][category_id]).forEach(item_id => {
                    Object.keys(this.shopCarts[shopId][category_id][item_id]).forEach(food_id => {
                            let foodItem = this.shopCarts[shopId][category_id][item_id][food_id];
                            let newFoodItem = {...foodItem, category_id, item_id, food_id, shopId}
                            if (newFoodItem.shopId == this.shopId) {
                                    this.carArr.push(newFoodItem);
                                }
                            });
                        })
                    })
                })
    // console.log(this.carArr)
    for (let i = 0; i < this.carArr.length; i++) {
        let p = [{attrs:[],extra:{},id:this.carArr[i].id,name:this.carArr[i].name,price:this.carArr[i].price,packing_fee:this.carArr[i].packing_fee,quantity:this.carArr[i].num,sku_id:this.carArr[i].sku_id,specs:this.carArr[i].specs,stock:this.carArr[i].stock,}]
        this.entities.push(p)
    }
    // console.log(this.entities)
    this.axios.post("/v1/carts/checkout",{
            restaurant_id:this.shopId,
            geohash:this.geohash,
            entities:this.entities
        }).then(res=>{
            console.log(res)
            this.distObj = res;
            this.canhe = res.cart.extra[0].price;
            this.peisong = res.cart.deliver_amount;
            localStorage.setItem('cartid',res.id)
            // console.log(typeof this.peisong)
        }).catch(err => {})
},
methods:{
    goback(){
        this.isShow = false;
    },
    choosefu(){
        this.isShow = true;
    },
    goxiadan(){
        this.$router.push('/payment')
    }
}
}
</script>

